.survey-info-bar {
  margin-top: 10px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  background: rgba(255,255,255,0.15);
  padding: 8px 14px;
  border-radius: 10px;
  color: #fff;
  font-weight: 600;
  backdrop-filter: blur(4px);
}
.survey-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  min-height: 100vh;
}

.loading-screen {
  text-align: center;
  padding: 3rem;
  font-size: 1.2rem;
  color: #666;
}

.jump-btn {
  background: rgba(255, 200, 50, 0.45);
  color: #fff;
  padding: 12px 20px;
  border-radius: 40px;
  font-size: 1rem;
  font-weight: 700;
  border: none;
  cursor: pointer;
  transition: 0.2s ease;
}

.jump-btn:hover {
  background: rgba(255, 170, 0, 0.55);
}

/* Ï†úÏ∂ú ÌôïÏù∏ ÌôîÎ©¥ Ïä§ÌÉÄÏùº */
.submit-confirm-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 90vh;
  padding: 20px;
}

.submit-card {
  background: white;
  border-radius: 20px;
  padding: 40px;
  max-width: 500px;
  width: 100%;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  text-align: center;
  animation: slideUp 0.5s ease-out;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.check-icon {
  font-size: 80px;
  margin-bottom: 20px;
  animation: bounce 1s ease-in-out;
}

@keyframes bounce {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.2); }
}

.submit-card h2 {
  font-size: 32px;
  color: #333;
  margin-bottom: 15px;
  font-weight: 700;
}

.confirm-message {
  font-size: 16px;
  color: #666;
  line-height: 1.6;
  margin-bottom: 30px;
}

.stats-summary {
  display: flex;
  justify-content: space-around;
  gap: 20px;
  margin: 30px 0;
  padding: 20px;
  background: linear-gradient(135deg, #667eea15 0%, #764ba215 100%);
  border-radius: 15px;
}

.stat-item {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.stat-label {
  font-size: 12px;
  color: #888;
  font-weight: 500;
}

.stat-value {
  font-size: 24px;
  font-weight: 700;
  color: #667eea;
}

.submit-buttons {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: 30px;
}

.submit-btn {
  padding: 16px 32px;
  font-size: 16px;
  font-weight: 600;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 100%;
}

.submit-btn.primary {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4);
}

.submit-btn.primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.6);
}

.submit-btn.primary:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.submit-btn.secondary {
  background: white;
  color: #667eea;
  border: 2px solid #667eea;
}

.submit-btn.secondary:hover:not(:disabled) {
  background: #f5f7ff;
}

/* Î™®Î∞îÏùº Î∞òÏùëÌòï */
@media (max-width: 768px) {
  .submit-card {
    padding: 30px 20px;
  }

  .submit-card h2 {
    font-size: 24px;
  }

  .check-icon {
    font-size: 60px;
  }

  .stats-summary {
    flex-direction: column;
    gap: 15px;
  }

  .stat-value {
    font-size: 20px;
  }

  .submit-btn {
    padding: 14px 24px;
    font-size: 14px;
  }
}
