/* Ï†ÑÎ©¥ Í¥ëÍ≥† Ïä§ÌÉÄÏùº Í∞êÏÇ¨ Î©îÏãúÏßÄ */
.end-scene.fullscreen-ad {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  margin: 0;
  overflow-y: auto;
  animation: fadeIn 0.8s ease-out;
  z-index: 9999;
}

/* Î©îÏù∏ Í∞êÏÇ¨ Î©îÏãúÏßÄ */
.main-message {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 60px 20px;
  text-align: center;
  transition: all 0.8s ease;
}

.main-message.slide-up {
  transform: translateY(-50px);
  opacity: 0.3;
  scale: 0.9;
}

.celebration-emoji {
  font-size: 120px;
  margin-bottom: 30px;
  animation: bounceIn 1s ease-out, float 3s ease-in-out infinite;
  filter: drop-shadow(0 10px 30px rgba(0, 0, 0, 0.3));
}

@keyframes bounceIn {
  0% { transform: scale(0); opacity: 0; }
  50% { transform: scale(1.2); }
  100% { transform: scale(1); opacity: 1; }
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-20px); }
}

.main-title {
  font-size: 56px;
  font-weight: 900;
  color: white;
  margin-bottom: 40px;
  text-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  animation: slideInDown 0.8s ease-out;
  letter-spacing: -1px;
}

.main-thank-you {
  max-width: 800px;
  margin: 0 auto;
  animation: fadeInUp 1s ease-out 0.3s both;
}

.message-line {
  font-size: 28px;
  color: white;
  margin: 15px 0;
  font-weight: 500;
  line-height: 1.6;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.2);
  animation: fadeInUp 0.6s ease-out both;
}

.message-line:nth-child(1) { animation-delay: 0.4s; }
.message-line:nth-child(2) { animation-delay: 0.6s; }
.message-line:nth-child(3) { animation-delay: 0.8s; }
.message-line:nth-child(4) { animation-delay: 1s; }

.message-line.highlight {
  font-size: 32px;
  font-weight: 700;
  color: #FFD700;
  text-shadow: 0 0 20px rgba(255, 215, 0, 0.5);
}

.gratitude-text {
  font-size: 36px;
  font-weight: 800;
  color: #FFD700;
  margin-top: 40px;
  text-shadow: 0 0 30px rgba(255, 215, 0, 0.6);
  animation: pulse 2s ease-in-out infinite, fadeInUp 1.2s ease-out both;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInDown {
  from {
    opacity: 0;
    transform: translateY(-50px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Î™®Îì† Ïñ∏Ïñ¥ ÌëúÏãú ÏòÅÏó≠ */
.all-languages-container {
  flex: 1;
  width: 100%;
  max-width: 1400px;
  padding: 40px 20px;
  animation: fadeIn 1s ease-out;
}

.languages-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  width: 100%;
}

.language-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  padding: 30px;
  text-align: center;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.2);
  transition: all 0.4s ease;
  animation: scaleIn 0.5s ease-out both;
  position: relative;
  overflow: hidden;
}

.language-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);
  transition: left 0.6s ease;
}

.language-card:hover::before {
  left: 100%;
}

.language-card:nth-child(1) { animation-delay: 0.1s; }
.language-card:nth-child(2) { animation-delay: 0.2s; }
.language-card:nth-child(3) { animation-delay: 0.3s; }
.language-card:nth-child(4) { animation-delay: 0.4s; }
.language-card:nth-child(5) { animation-delay: 0.5s; }
.language-card:nth-child(6) { animation-delay: 0.6s; }
.language-card:nth-child(7) { animation-delay: 0.7s; }
.language-card:nth-child(8) { animation-delay: 0.8s; }

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.8);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.language-card:hover {
  transform: translateY(-10px) scale(1.05);
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.3);
}

.language-card.active-lang {
  background: linear-gradient(135deg, #FFD700 0%, #FFA500 100%);
  border: 3px solid white;
  box-shadow: 0 12px 40px rgba(255, 215, 0, 0.5);
  animation: glow 2s ease-in-out infinite;
}

@keyframes glow {
  0%, 100% {
    box-shadow: 0 12px 40px rgba(255, 215, 0, 0.5);
  }
  50% {
    box-shadow: 0 12px 60px rgba(255, 215, 0, 0.8);
  }
}

.lang-code {
  display: inline-block;
  padding: 8px 16px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 700;
  margin-bottom: 15px;
  letter-spacing: 1px;
}

.active-lang .lang-code {
  background: white;
  color: #FFA500;
}

.lang-title {
  font-size: 22px;
  font-weight: 800;
  color: #333;
  margin-bottom: 12px;
  line-height: 1.3;
}

.active-lang .lang-title {
  color: white;
}

.lang-gratitude {
  font-size: 16px;
  font-weight: 600;
  color: #667eea;
  line-height: 1.5;
}

.active-lang .lang-gratitude {
  color: white;
  font-weight: 700;
}

/* ÌïòÎã® Í≥†Ï†ï ÏòÅÏó≠ */
.bottom-fixed {
  position: sticky;
  bottom: 0;
  width: 100%;
  background: rgba(255, 255, 255, 0.98);
  padding: 30px 20px;
  box-shadow: 0 -10px 40px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
  z-index: 10;
}

.auto-redirect {
  margin-bottom: 20px;
  padding: 18px;
  background: linear-gradient(135deg, #fff3cd 0%, #ffe69c 100%);
  border-radius: 15px;
  border: 3px solid #ffc107;
  box-shadow: 0 4px 15px rgba(255, 193, 7, 0.3);
}

.redirect-text {
  font-size: 18px;
  color: #856404;
  font-weight: 700;
  margin: 0;
  text-align: center;
}

.countdown {
  display: inline-block;
  font-size: 32px;
  font-weight: 900;
  color: #ff6b35;
  margin: 0 8px;
  animation: pulse 1s ease-in-out infinite;
  text-shadow: 0 2px 10px rgba(255, 107, 53, 0.4);
}

@keyframes pulse {
  0%, 100% { transform: scale(1); opacity: 1; }
  50% { transform: scale(1.3); opacity: 0.8; }
}

.end-buttons {
  display: flex;
  gap: 15px;
  max-width: 600px;
  margin: 0 auto;
}

.end-btn {
  flex: 1;
  padding: 18px 32px;
  border-radius: 15px;
  border: none;
  font-size: 17px;
  cursor: pointer;
  font-weight: 700;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  font-family: inherit;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.end-btn.primary {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: #fff;
}

.end-btn.primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.5);
}

.end-btn.secondary {
  background: #fff;
  color: #667eea;
  border: 3px solid #667eea;
}

.end-btn.secondary:hover {
  background: #667eea;
  color: #fff;
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.4);
}

/* Ïï†ÎãàÎ©îÏù¥ÏÖò */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Î™®Î∞îÏùº ÏµúÏ†ÅÌôî */
@media (max-width: 768px) {
  .main-message {
    padding: 40px 15px;
  }

  .celebration-emoji {
    font-size: 80px;
    margin-bottom: 20px;
  }

  .main-title {
    font-size: 32px;
    margin-bottom: 25px;
  }

  .message-line {
    font-size: 18px;
    margin: 10px 0;
  }

  .message-line.highlight {
    font-size: 20px;
  }

  .gratitude-text {
    font-size: 24px;
    margin-top: 25px;
  }

  .all-languages-container {
    padding: 20px 10px;
  }

  .languages-grid {
    grid-template-columns: 1fr;
    gap: 15px;
  }

  .language-card {
    padding: 20px;
  }

  .lang-title {
    font-size: 18px;
  }

  .lang-gratitude {
    font-size: 14px;
  }

  .bottom-fixed {
    padding: 20px 15px;
  }

  .redirect-text {
    font-size: 15px;
  }

  .countdown {
    font-size: 24px;
  }

  .end-buttons {
    flex-direction: column;
    gap: 12px;
  }

  .end-btn {
    padding: 15px 24px;
    font-size: 15px;
  }
}

/* ÌÉúÎ∏îÎ¶ø */
@media (min-width: 769px) and (max-width: 1024px) {
  .languages-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .main-title {
    font-size: 48px;
  }

  .message-line {
    font-size: 24px;
  }

  .message-line.highlight {
    font-size: 28px;
  }
}

/* ÌÅ∞ ÌôîÎ©¥ */
@media (min-width: 1200px) {
  .languages-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
